import React from 'react';
import LineLink from '../../components/LineLink';
import TabMenu from '../../components/TabMenu';
import './Index.css';

const User = () => {
  return (
    <div className="user">
      <div className="user__top">
        <div className="tOperator">
          <div className="tOperator__icon tOperator__icon--setting" />
          <div className="tOperator__icon tOperator__icon--message" />
        </div>
        <div className="user__info">
          <div className="user__avatar" style={{ backgroundImage: 'url(/source/avatar.jpeg)' }} />
          <div className="user__detail">
            <div className="user__name"></div>
            <div>
              <span className="user__fellow"> 100</span>
              <span className="user__fans"> 10</span>
            </div>
            <div className="user__level"></div>
          </div>
        </div>
      </div>
      <div className="user__content">
        <LineLink title="" extra="20" href="#" />
        <LineLink title="" extra="20" href="#" />
        <LineLink title="xxx" extra="20" href="#" />
      </div>
      <TabMenu current="user" />
    </div>
  );
};

export default User;



