import React from 'react';
import Star from '../../../components/Star';
import './ScoreSummary.css';

const ScoreSummary = () => {
  return (
    <div className="scoreSummary">
      <div className="scoreSummary__top">
        <div className="summaryItem">
          <div className="summaryItem__value">9.3 <Star value={4} /></div>
          <div className="summaryItem__title">12313</div>
        </div>
        <div className="summaryItem">
          <div className="summaryItem__value">80%</div>
          <div className="summaryItem__title">V</div>
        </div>
        <div className="summaryItem">
          <div className="summaryItem__value">87723</div>
          <div className="summaryItem__title"></div>
        </div>
      </div>
      <div className="summaryBtn">
        <button className="summaryBtn__item summaryBtn__item--want"><i /> </button>
        <button className="summaryBtn__item summaryBtn__item--seen"><i /> </button>
      </div>
    </div>
  );
};

export default ScoreSummary;



